import Browser from 'webextension-polyfill';

(() => {
  if (window.browser) {
    void Browser.permissions
      .contains({ origins: ['*://*.toggl.com/*', '*://toggl.com/*'] })
      .then(res => {
        if (!res)
          void Browser.tabs.update({
            url: Browser.runtime.getURL(
              'src/pages/requestPermission/index.html'
            ),
          });
      });

    const FFEl = document.getElementById('FFPin');
    const chromeEl = document.getElementById('chromePin');

    if (FFEl) FFEl.style.display = 'block';
    if (chromeEl) chromeEl.style.display = 'none';
  }
})();
